import React, { useState } from 'react';
import { Printer, User, QrCode } from 'lucide-react';
import * as Types from '@/lib/types';
import { Button } from '@/components/ui/button';
import { Select } from '@/components/ui/select';

interface IDCardViewProps {
    students: Types.Student[];
    classes: Types.Class[];
    settings: Types.Settings;
}

export const IDCardView: React.FC<IDCardViewProps> = ({ students, classes, settings }) => {
    const [selectedClass, setSelectedClass] = useState(classes[0]?.id || '');
    const activeStudents = students.filter(s => s.class_id === selectedClass);
    const currentClass = classes.find(c => c.id === selectedClass);

    return (
        <div className="space-y-6">
            <div className="flex justify-between items-center no-print">
                <div><h1 className="text-2xl font-bold text-gray-900">Student ID Cards</h1><p className="text-gray-500">Generate and print student identity cards</p></div>
                <div className="flex gap-4"><Select className="w-48" value={selectedClass} onChange={e => setSelectedClass(e.target.value)}>{classes.map(c => <option key={c.id} value={c.id}>{c.name}</option>)}</Select><Button onClick={() => window.print()}><Printer className="h-4 w-4 mr-2" /> Print Batch</Button></div>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-8 print:grid-cols-2 print:gap-4 print:w-full">
                {activeStudents.map(s => (
                    <div key={s.id} className="relative w-full aspect-[1.586] bg-white rounded-xl overflow-hidden border border-gray-200 shadow-sm print:shadow-none print:border-gray-900 break-inside-avoid">
                        <div className="absolute top-0 w-full h-1/3 bg-brand-600 flex items-center px-6">
                            <div className="flex-1"><h3 className="text-white font-bold text-lg uppercase tracking-wider">{settings.school_name}</h3><p className="text-brand-100 text-xs">{settings.school_tagline}</p></div>
                            {settings.logo_media && <img src={settings.logo_media} className="h-12 w-12 object-contain bg-white rounded-full p-1" />}
                        </div>
                        <div className="absolute top-1/3 w-full h-2/3 p-6 flex gap-6 items-center">
                            <div className="h-24 w-24 rounded-lg bg-gray-100 border-2 border-brand-100 overflow-hidden flex-shrink-0">{s.passport_media ? <img src={s.passport_media} className="h-full w-full object-cover" /> : <User className="h-full w-full p-4 text-gray-300" />}</div>
                            <div className="flex-1 space-y-1">
                                <h2 className="text-xl font-bold text-gray-900 uppercase">{s.names}</h2>
                                <div className="text-sm text-gray-600"><span className="font-bold text-gray-400 uppercase text-xs block">ID Number</span>{s.student_no}</div>
                                <div className="text-sm text-gray-600"><span className="font-bold text-gray-400 uppercase text-xs block">Class</span>{currentClass?.name}</div>
                            </div>
                            <div className="h-16 w-16 bg-gray-900 flex items-center justify-center text-white"><QrCode className="h-10 w-10" /></div>
                        </div>
                        <div className="absolute bottom-0 w-full h-8 bg-brand-900 flex items-center justify-center"><p className="text-brand-100 text-[10px] uppercase tracking-widest">Student Identity Card</p></div>
                    </div>
                ))}
                {activeStudents.length === 0 && <div className="col-span-full text-center py-12 text-gray-400 italic">No students found in this class.</div>}
            </div>
        </div>
    );
};
